/**
 * API Service
 * 
 * Handles communication with the Alpha Scanner API
 */

import type {
  Opportunity,
  FeatureScores,
  TradeSetup,
  RiskMetrics,
} from '@alpha-scanner/shared'

// API configuration
const API_BASE_URL = import.meta.env.VITE_API_URL || 'http://localhost:8000'

// API response types
export interface OpportunitiesResponse {
  opportunities: Opportunity[]
  total: number
  limit: number
  offset: number
  timestamp: string
}

export interface FetchOpportunitiesParams {
  limit?: number
  offset?: number
  min_score?: number
  symbol?: string
}

// Generic API error class
export class ApiError extends Error {
  constructor(
    message: string,
    public status?: number,
    public response?: Response
  ) {
    super(message)
    this.name = 'ApiError'
  }
}

// Generic fetch wrapper with error handling
async function apiFetch<T>(endpoint: string, options?: RequestInit): Promise<T> {
  const url = `${API_BASE_URL}/api/v1${endpoint}`
  
  try {
    const response = await fetch(url, {
      headers: {
        'Content-Type': 'application/json',
        ...options?.headers,
      },
      ...options,
    })

    if (!response.ok) {
      const errorText = await response.text()
      throw new ApiError(
        `API request failed: ${response.status} ${response.statusText}${errorText ? ` - ${errorText}` : ''}`,
        response.status,
        response
      )
    }

    return await response.json()
  } catch (error) {
    if (error instanceof ApiError) {
      throw error
    }
    
    // Network or other errors
    throw new ApiError(
      `Network error: ${error instanceof Error ? error.message : 'Unknown error'}`
    )
  }
}

/**
 * Fetch trading opportunities from the API
 */
export const fetchOpportunities = async (
  params: FetchOpportunitiesParams = {}
): Promise<OpportunitiesResponse> => {
  const searchParams = new URLSearchParams()
  
  if (params.limit) searchParams.set('limit', params.limit.toString())
  if (params.offset) searchParams.set('offset', params.offset.toString())
  if (params.min_score) searchParams.set('min_score', params.min_score.toString())
  if (params.symbol) searchParams.set('symbol', params.symbol)
  
  const queryString = searchParams.toString()
  const endpoint = `/opportunities${queryString ? `?${queryString}` : ''}`
  
  return apiFetch<OpportunitiesResponse>(endpoint)
}

/**
 * Fetch a specific opportunity by symbol
 */
export const fetchOpportunityBySymbol = async (symbol: string): Promise<Opportunity> => {
  return apiFetch<Opportunity>(`/opportunities/${symbol}`)
}

/**
 * Check API health
 */
export const checkHealth = async (): Promise<{
  status: string
  timestamp: string
  version: string
  environment: string
  database_connected: boolean
  redis_connected: boolean
}> => {
  return apiFetch('/health')
}

// Export types for use in components
export type { Opportunity, FeatureScores, TradeSetup, RiskMetrics }